
body,html{margin:0;padding:0
}
#app{position:absolute;width:100%
}
#deps-arrow{position:absolute;height:800px;width:1200px
}
.graph-container{position:relative;display:block;height:800px;width:1200px;background-color:#f8f8f8;overflow:auto
}
.graph-container,.level-container{box-sizing:border-box;padding:0;margin:0
}
.graph-container li.graph-level,.level-container li.graph-level{position:absolute;box-sizing:border-box;list-style:none;width:200px;padding:0 35px;list-style:none
}
.level-container li{background-color:#e3e3e3;padding:0;margin:0;margin:70px 0;padding:12px 12px;text-align:center;cursor:pointer;list-style:none
}



